.slider {
 flex-grow: 1;
  padding: 32px 25px 32px 20px;
  background-color: rgb(255, 255, 255);
  max-height: 100%;
  max-width: 100%;
}
.slider .swiper_container1 {
  width: 100%;
  height: 100%;
}
.slider .swiper_container2 {
  width: 100%;
  height: 100%;
}
.slider__flex {
  display: flex;
  align-items: flex-start;
}
.slider__col {
  width: 100%;
  height: 500px;
  margin-right: 30px;
}
.slider__thumbs {
  width: 50px;
  height: 100%;
}
.slider__images {
  max-height: 700px;
  max-width: 400px;
}
.slider__images .slider__image img {
  transition: 3s;
}
.slider__images .slider__image:hover img {
  transform: scale(1.1);
}
.slider__image {
  width: 100%;
  height: 100%;
  overflow: hidden;
  cursor: zoom-in;
}
.slider__image img {
  margin: 0 auto;
  display: block;
  max-width: 100%;
  max-height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}
@media (max-width: 767.98px) {
  .slider__flex {
    flex-direction: column-reverse;
  }
  .slider__col {
    flex-direction: row;
    align-items: center;
    margin-right: 0;
    margin-top: 24px;
    width: 100%;
  }
  .slider__images {
    width: 100%;
  }
  .slider__thumbs {
    height: 100px;
    width: calc(100% - 96px);
    margin: 0 16px;
  }
}
