.rating{display:flex;align-items:center;border:1px solid var(--light-gray);border-radius:8px;font-size:20px;line-height:.75;padding:6px;max-width:125px}.rating__body{position:relative}.rating__body::before{content:"★★★★★";display:block;color:var(--light-gray)}.rating__active{position:absolute;width:0%;height:100%;top:0;left:0;overflow:hidden}.rating__active::before{content:"★★★★★";position:absolute;width:100%;height:100%;top:0;left:0;color:var(--orange)}.rating__count{padding-left:10px;font-size:70%;color:#303030;opacity:1}.rating:hover{border-color:var(--green);cursor:pointer}